.ViewMyResume{
    background-color: #fff;
    border: none;
    padding: 12px 24px;
    font-size: 1.2em;
    border-radius: 25px;
    font-family: 'Open Sans';
    color: #04456C;
    display: flex;
    align-items: center;
    font-weight: bold;
    gap: 8px;
    box-shadow: 8px 8px 16px #05223C;
}

.ViewMyResume:active{
    box-shadow: 10px 10px 20px #05223C;
    background-color: #e9e9e9;
}