@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::-webkit-scrollbar {
  width: 0.5em;
  height: 0.5em;
  padding: 1px;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #dba800;
  border-radius: 8px;
  outline: 3px solid #fff;
}
.App{
  display: -webkit-flex;
  display: flex;
  margin: 0;
  padding: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-sizing: border-box;
}
.Hero{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background-image: linear-gradient(to right top, #051937, #133961, #1c5d8e, #1d85bd, #12afeb);
}

.Hero>*{
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.ViewMyResume{
    position: absolute;
    bottom:100px;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
}
.Home{
    display: -webkit-flex;
    display: flex;
    height:100vh;
    width: 50vw;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column; 
    -webkit-align-items: flex-start; 
            align-items: flex-start;
    padding: 100px;
    padding-bottom: 200px;
    box-sizing: border-box;
    margin: 0;
}

.title{
    font-size: 3em;
    font-family: 'Josefin Sans';
    color:#fff;
    min-width: 300px;
    max-width: 500px;
    font-weight: 200;
    margin: 0;
}

.subtitle{
    font-size: 1.5em;
    color: #fff;
    font-family: 'Open Sans';
}
.ContactMe{
    background-color: #04436A;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height:100vh;
    width: 50vw;
    margin: 0;
    padding: 0;
    grid-gap: 20px;
    gap: 20px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding-bottom: 150px;
    box-sizing: border-box;
}

.ContactItemContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    grid-gap: 20px;
    gap: 20px;
}
.contactMeTitle{
    font-size: 1em;
    font-weight: bold;
    color:#fff;
    font-family: 'Open Sans';
}
.ContactItem{
    padding: 16px 24px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 50px;
}
a{
    text-decoration: none;
    text-transform: none;
    color: #051C3B;
}
.icon{
    width:30px;
}

.ContactItem p{
    font-size: 1.2em;
    font-family: 'Open Sans';
    margin: 0;
}
.ViewMyResume{
    background-color: #fff;
    border: none;
    padding: 12px 24px;
    font-size: 1.2em;
    border-radius: 25px;
    font-family: 'Open Sans';
    color: #04456C;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    grid-gap: 8px;
    gap: 8px;
    box-shadow: 8px 8px 16px #05223C;
}

.ViewMyResume:active{
    box-shadow: 10px 10px 20px #05223C;
    background-color: #e9e9e9;
}
.AboutMe{
    height: 100vh;
    width:100vw;
    margin: 0px;
    background-image: linear-gradient(to right top, #051937, #133961, #1c5d8e, #1d85bd, #12afeb);
    padding: 0px;
    width: 100vw;
    padding: 100px;
    padding-top: 50px;
    box-sizing: border-box;
}

.aboutMeTitle{
    font-size: 2em;
    font-family: 'Josefin Sans';
    font-weight: bold;
    color:#fff;
}

.aboutMeText{
    font-size: 1.2em;
    line-height: 1.8em;
    font-family: 'Open Sans';
    color: #fff;
}

.PhotoFrame{
    border-radius:20% 0% 20% 80%;
    height: 40vw;
    width:40vw;
    margin-top: -50px;
    margin-right: -100px;
    float: right;
    overflow: hidden;
    background-image: linear-gradient(to right top, #051937, #133961, #1c5d8e, #1d85bd, #12afeb);
}
