.ContactMe{
    background-color: #04436A;
    display: flex;
    flex-direction: column;
    height:100vh;
    width: 50vw;
    margin: 0;
    padding: 0;
    gap: 20px;
    align-items: center;
    justify-content: center;
    padding-bottom: 150px;
    box-sizing: border-box;
}

.ContactItemContainer{
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    gap: 20px;
}
.contactMeTitle{
    font-size: 1em;
    font-weight: bold;
    color:#fff;
    font-family: 'Open Sans';
}