.ContactItem{
    padding: 16px 24px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 50px;
}
a{
    text-decoration: none;
    text-transform: none;
    color: #051C3B;
}
.icon{
    width:30px;
}

.ContactItem p{
    font-size: 1.2em;
    font-family: 'Open Sans';
    margin: 0;
}