.AboutMe{
    height: 100vh;
    width:100vw;
    margin: 0px;
    background-image: linear-gradient(to right top, #051937, #133961, #1c5d8e, #1d85bd, #12afeb);
    padding: 0px;
    width: 100vw;
    padding: 100px;
    padding-top: 50px;
    box-sizing: border-box;
}

.aboutMeTitle{
    font-size: 2em;
    font-family: 'Josefin Sans';
    font-weight: bold;
    color:#fff;
}

.aboutMeText{
    font-size: 1.2em;
    line-height: 1.8em;
    font-family: 'Open Sans';
    color: #fff;
}

.PhotoFrame{
    border-radius:20% 0% 20% 80%;
    height: 40vw;
    width:40vw;
    margin-top: -50px;
    margin-right: -100px;
    float: right;
    overflow: hidden;
    background-image: linear-gradient(to right top, #051937, #133961, #1c5d8e, #1d85bd, #12afeb);
}