.Home{
    display: flex;
    height:100vh;
    width: 50vw;
    align-items: center;
    justify-content: center;
    flex-direction: column; 
    align-items: flex-start;
    padding: 100px;
    padding-bottom: 200px;
    box-sizing: border-box;
    margin: 0;
}

.title{
    font-size: 3em;
    font-family: 'Josefin Sans';
    color:#fff;
    min-width: 300px;
    max-width: 500px;
    font-weight: 200;
    margin: 0;
}

.subtitle{
    font-size: 1.5em;
    color: #fff;
    font-family: 'Open Sans';
}